.App {
    text-align: center;
    border: 3px solid black;
    -ms-flex-direction: column;
        flex-direction: column;
}

.App-header {
    background-color: #222;
    padding: 5px;
    color: white;
}

.App-bomb {
    -webkit-animation: App-logo-spin infinite 1s linear;
            animation: App-logo-spin infinite 1s linear;
    height: 50px;
}

.App-title {
    font-size: 40px;
}

.Badge {
    /* height: 150px; */
    padding: 10px;
}

.ProbAll {
    padding: 10px;
    /* height: 205px; */
}

.ProbNum {
    font-size: 60px;
    /* margin: 5px; */
    padding: 10px;
}

.ProbSymb {
    font-size: 60px;
    margin-left: 5px;
    margin-right: 5px;
    margin-bottom: 8px;
}

.ProbSpace {
    /* margin-top: 10%; */
    padding: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.ScoreBoard {
    /* margin-top: 10%; */
    font-size: 60px;
    color: green;
    /* margin-left: 5px;
    margin-right: 5px;
    margin-bottom: 8px; */
}

.ProbNotification {
    font-size: 30px;
    color: green;
    padding: 20px;
}

.KeyBoard {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 5px;
    /* margin-top: 10%;
    margin-left: 15px;
    margin-right: 15px; */
}

.keyBoardContainer {
    border-top: 3px solid black;
    padding: 10px;
}

.KeyBoardKey {
    width: 60px;
    height: 60px;
    background-color: #222;
    font-size: 25px;
    color: white;
    margin: 5px;
}

.ClearButton {
    /* margin-top: 15px; */
    height: 75px;
    width: 125px;
    background-color: #222;
    font-size: 25px;
    color: white;
}

.keyBoardBreak {
    /* margin-top: 100px;
    margin-bottom: 10px; */
    border-top: 2px solid #222;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}



